  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Manrope", sans-serif;
}

html {
  font-size: 100%;
}

body {
  min-height: 100vh;
  background-color: rgb(5, 110, 114);
  position: relative;
}
body main {
  min-height: 100vh;
  padding: 1.875rem;
}

h1 {
  font-size: 2rem;
  font-weight: 700;
  color: white;
  font-family: "Quicksand", sans-serif;
}
@media (min-width: 48rem) {
  h1 {
    font-size: 2.25rem;
  }
}
@media (min-width: 62rem) {
  h1 {
    font-size: 2.5rem;
  }
}
@media (min-width: 87.5rem) {
  h1 {
    font-size: 3rem;
  }
}

button {
  font-size: 1rem;
  font-weight: 600;
  color: white;
  background-color: rgb(5, 110, 114);
  border: none;
}

button:hover {
  background-color: rgba(5, 110, 114, 0.616);
  cursor: pointer;
}

@keyframes fade-slide-in {
  from {
    transform: translateY(-5rem);
  }
  to {
    transform: translateY(0);
  }
}
.flex-col {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.flex-col__gap15 {
  gap: 0.9375rem;
}
.flex-col__gap20 {
  gap: 1.25rem;
}
.flex-col__gap50 {
  gap: 5.625rem;
}
.flex-col__gap70 {
  gap: 4.375rem;
}

.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.flex-row__gap10 {
  gap: 0.625rem;
}

.widgets-list {
  width: 100%;
  height: 100%;
  overflow: scroll;
  overflow-x: hidden;
  display: grid;
  align-items: center;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  gap: 1.25rem;
  padding: 0.625rem;
}
@media (min-width: 48rem) {
  .widgets-list {
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 62rem) {
  .widgets-list {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 75rem) {
  .widgets-list {
    grid-template-columns: repeat(4, 1fr);
  }
}

.commands-container {
  width: 100%;
  padding: 1rem;
}

.window-overlay {
  min-height: 100%;
  width: 100%;
  padding: 1.25rem;
  position: absolute;
  top: 0;
  z-index: 2000;
  background-color: rgba(5, 110, 114, 0.616);
}

.alert-box-overlay {
  min-height: 100%;
  width: 100%;
  padding: 1.25rem;
  position: absolute;
  top: 0;
  z-index: 4000;
  background-color: rgba(85, 85, 85, 0.459);
}

.alert-box {
  width: 75%;
  height: 25%;
  padding: 1.5625rem;
  text-align: center;
  line-height: 1.25rem;
  border-radius: 1.25rem;
  background-color: white;
  z-index: 100000;
  box-shadow: 0.09375rem 0.09375rem 0.1875rem rgba(146, 144, 144, 0.603);
  animation: fade-slide-in 0.3s ease-out forwards;
}
@media (min-width: 36rem) {
  .alert-box {
    width: 60%;
    height: 30%;
  }
}
@media (min-width: 48rem) {
  .alert-box {
    width: 40%;
  }
}
@media (min-width: 62rem) {
  .alert-box {
    width: 30%;
  }
}
.alert-box__message {
  font-size: 0.875rem;
  font-weight: 600;
  line-height: 1.5625rem;
}
.alert-box__message--green-mess {
  color: rgb(10, 167, 10);
}
.alert-box__message--blue-grey {
  color: rgb(5, 110, 114);
}
.alert-box__button {
  width: 3.75rem;
  height: 2.8125rem;
  border-radius: 3.125rem;
  background-color: rgb(5, 110, 114);
  color: white;
  font-size: 0.875rem;
  font-weight: 600;
}

.view-note-btn {
  width: 6.25rem;
  height: 3.125rem;
  border-radius: 1.625rem;
  background-color: white;
  color: rgb(5, 110, 114);
  font-size: 0.875rem;
}
@media (min-width: 48rem) {
  .view-note-btn {
    width: 6.5625rem;
    height: 3.4375rem;
    border-radius: 1.75rem;
  }
}
.view-note-btn:hover {
  background-color: rgba(255, 255, 255, 0.747);
}

.search {
  display: flex;
  width: min(100%, 400px);
  background-color: white;
  border-radius: 1.875rem;
  justify-content: space-around;
  align-items: center;
  height: 2.5rem;
}
@media (min-width: 36rem) {
  .search {
    height: 2.8125rem;
  }
}
@media (min-width: 48rem) {
  .search {
    height: 3.125rem;
  }
}
.search__field {
  border-radius: 1.875rem;
  width: 80%;
  height: 100%;
  background-color: white;
  outline: none;
  padding: 0.875rem;
  border: none;
}
.search__btn {
  font-size: 1.125rem;
  height: 1.875rem;
  width: 1.875rem;
  border-radius: 100%;
  color: white;
  background-color: rgb(5, 110, 114);
}
@media (min-width: 36rem) {
  .search__btn {
    height: 2.1875rem;
    width: 2.1875rem;
  }
}
@media (min-width: 48rem) {
  .search__btn {
    height: 2.5rem;
    width: 2.5rem;
  }
}

.writing-area {
  min-height: 90%;
  min-width: 85%;
  margin-top: 1.375rem;
}
.writing-area__title {
  width: 100%;
  border-radius: 0.625rem;
  border: 0.0625rem solid rgba(146, 144, 144, 0.603);
  outline: none;
  padding: 0.625rem;
  height: 10%;
  margin-bottom: 0.9375rem;
  font-weight: 600;
}
.writing-area__title:focus {
  box-shadow: 0.09375rem 0.09375rem 0.1875rem rgba(146, 144, 144, 0.603);
}
.writing-area__text {
  width: 100%;
  border-radius: 0.625rem;
  border: 0.0625rem solid rgba(146, 144, 144, 0.603);
  outline: none;
  padding: 0.625rem;
  height: 100%;
  font-weight: 500;
  margin-bottom: 0.9375rem;
}
.writing-area__text:focus {
  box-shadow: 0.09375rem 0.09375rem 0.1875rem rgba(146, 144, 144, 0.603);
}
.writing-area__btn {
  height: 3.125rem;
  width: 5rem;
  border-radius: 1.125rem;
  font-size: 0.875rem;
  outline: none;
}
@media (min-width: 48rem) {
  .writing-area__btn {
    height: 3.4375rem;
    width: 5.3125rem;
    border-radius: 1.25rem;
  }
}

.create-note__label {
  text-align: center;
  font-size: 1rem;
  color: white;
  font-weight: 500;
}
@media (min-width: 87.5rem) {
  .create-note__label {
    font-size: 1.125rem;
  }
}
.create-note__icon {
  width: 5rem;
  height: 5rem;
  border: 0.156rem dashed white;
  border-radius: 6.25rem;
  cursor: pointer;
}
@media (min-width: 48rem) {
  .create-note__icon {
    width: 5.3125rem;
    height: 5.3125rem;
  }
}
.create-note__icon:hover {
  border: 2.5px dashed rgba(255, 255, 255, 0.747);
}
.create-note__cross {
  font-size: 1.75rem;
  font-weight: 500;
  color: white;
}

.closing-cross {
  font-size: 1.375rem;
  top: 0.3125rem;
  right: 0.75rem;
  padding: 0.3125rem;
  font-family: monospace;
  position: absolute;
  cursor: pointer;
}
.closing-cross--dark-grey {
  color: rgb(95, 93, 93);
  font-weight: 500;
}
.closing-cross--dark-grey:hover {
  color: rgba(95, 93, 93, 0.514);
}
.closing-cross--white {
  color: white;
  font-weight: 600;
}
.closing-cross--white:hover {
  color: rgba(255, 255, 255, 0.747);
}

.note-widget {
  width: 100%;
  height: 12.5rem;
  padding: 1.875rem;
  background-color: rgb(129, 240, 206);
  margin: auto;
  max-width: 21.875rem;
  border-radius: 1.875rem;
  position: relative;
  cursor: pointer;
  user-select: none;
  text-align: center;
}
@media (min-width: 36rem) {
  .note-widget {
    max-width: 23.125rem;
    height: 13.75rem;
  }
}
.note-widget:hover {
  background-color: rgba(129, 240, 207, 0.644);
}
.note-widget__title {
  font-size: 1rem;
  font-weight: 600;
  color: rgb(5, 110, 114);
}
@media (min-width: 48rem) {
  .note-widget__title {
    font-size: 1.125rem;
  }
}
.note-widget__text {
  font-size: 0.875rem;
  color: rgb(33, 136, 139);
  font-weight: 500;
}
@media (min-width: 48rem) {
  .note-widget__text {
    font-size: 1rem;
  }
}
.note-widget__date {
  font-size: 0.875rem;
  color: white;
  font-weight: 500;
}
@media (min-width: 48rem) {
  .note-widget__date {
    font-size: 1rem;
  }
}

.window {
  height: 80vh;
  width: 95%;
  background-color: white;
  border-radius: 0.9375rem;
  animation: fade-slide-in 0.6s ease-out forwards;
}
@media (min-width: 36rem) {
  .window {
    height: 85vh;
  }
}
.window--writing {
  max-width: 31.25rem;
}
.window--note-viewing {
  padding: 2.8125rem;
}
@media (min-width: 62rem) {
  .window--note-viewing {
    width: 90%;
  }
